.wrapper {
    height: 100vh;
    width: 100%;
    // padding-top: 20vh;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 10px solid #7cb3f5;
}

.projectsContainer {
    height: 80%;
    width: 80%;
    align-items: center;
    justify-content: center;
    position: relative;
    display: flex;
    flex-direction: row;
    // gap: 2rem;
    color: black;

    ul {
        color: black;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 50px;
    }

    li {
        color: black;

        a {
            text-decoration: none;
            color: azure;
            background-color: #7cb3f5;
            font-family: "Tinos", serif !important;
        }
    }

}

.sideNavBackground,
.sideNavBackgroundActive {
    position: fixed;
    bottom: 0%;
    height: 15vh;
    width: 100vw;
    //  z-index: -1;
    //         background-color: #7cb3f5;
    // background-image: linear-gradient(62deg, #7cb3f5 0%, #a055fc 100%);
}

.sideNavBackground {
    background-color: transparent;
}

.sideNavBackgroundActive {
    background-color: #7cb3f5;
}




@media (max-width:960px) {
    .projectsContainer {
        ul {
            grid-template-columns: 1fr;
        }
    }
}